@import "src/styles/variables";

.carouselContainer {
    position: relative;
    width: 100%;
    height: 670px;
    background-color: #FCF8F3;
    margin-bottom: 67px;

    display: flex;
    align-items: center;

    @media (max-width: 1120px) {
        align-items: start;
        height: 100%;
        flex-direction: column;
    }
}

.textContainer{
    width: 422px;
    min-width: 422px;
    height: 224px;
    margin: 0 42px 0 100px; 

    @media (max-width: 1120px) {
        margin: 100px 0 100px 50px; 
    }

    @media (max-width: 715px) {
        height: 100%;
        width: 100%;
        min-width: 320px;
        margin: 50px 0 50px 25px; 
    }

    @media (max-width: 460px) {
        font-size: 2.8rem;
    }

    @media (max-width: 350px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 25px 0 25px 16px; 
        margin: 50px 0 50px 0;
    }
}

.firstLevelText{
    margin-bottom: 7px;
    font-family: var(--font-primary);
    font-size: 4.0rem;
    font-weight: 700;
    line-height: 48px;
    text-align: left;
    color:var(--secondary-font-color);

    @media (max-width: 515px) {
        font-size: 3.4em;
        line-height: 42px;
    }

    @media (max-width: 460px) {
        font-size: 2.8rem;
        line-height: 36px;
    }
}

.secondLevelText{
    margin-bottom: 25px;
    font-family: var(--font-primary);
    font-size: 1.6rem;
    font-weight: 500;
    line-height: 24px;
    text-align: left;
    color: #616161;

    @media (max-width: 515px) {
        font-size: 1.3rem;
        line-height: 20px;
    }
}

.sliderContainer{
    position: relative;
    width: 100%;
    min-width: 112%;

    @media (max-width: 1120px) {
        min-width: 1250px;
        margin: 0 0 0 50px; 
    }

    @media (max-width: 715px) {
        min-width: 900px;
        margin: 0 0 0 25px; 
    }

    @media (max-width: 515px) {
        min-width: 600px;
    }

    @media (max-width: 350px) {
        margin: 0 0 0 16px;
    }

}

.button{
    width: 176px;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;

    font-family: var(--font-primary);
    font-size: 1.6rem;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    color: #FFFFFF;

    &:hover {
        border: 2px solid skyblue;
    }
}

