.sliderContainer {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
}

  // children

.windowSlider{
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.allPagesContainer{
    width: 100%;
    height: 100%;
    display: grid;

    transition: translate;
    transition-property: transform;
    transition-duration: 300ms;
    transition-timing-function: ease-in-out;
}

.buttonContainer{
    position: absolute;
    left: 47.5%;
    display: flex;
} 

.buttonCircle {
    position: absolute;
    width: 48px;
    height: 48px;
    cursor: pointer;

    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
}

    // dots

.dotsContainer {
    position: absolute;
    bottom: 5%;
    left: 28%;

    max-width: max-content;
    min-width: min-content;
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }

  .dots {
    position: relative;
    width: 27px;
    height:27px;
    margin-right: 20px;
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

  }

  .activeDot{
    position: absolute;
  }
